.order-history {

}

.order-history__active {
  margin-bottom: 60px;
  @media (max-width: 768px) {
    margin-bottom: 32px;
  }
}
.order-history-card {
  background-color: var(--bg-element);
  padding: 15px 20px 20px;
  border-radius: 12px;
  margin-bottom: 8px;
  @media (max-width: 768px) {
    padding-left: 8px;
    padding-right: 8px
  }
}
.order-history-card:last-child {
  margin-bottom: 0;
}
.order-history-card__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  transition: all 0.3s ease;
  @media (max-width: 768px) {
    padding-left: 12px;
    padding-right: 12px;
  }
  @media (max-width: 420px) {
    flex-direction: column;
    align-items: flex-start;
  }
}
.order-history-card__id {
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  color: #000000;
  margin-bottom: 20px;
  @media (max-width: 768px) {
    margin-bottom: 16px
  }
}
.order-history-card__status {
  font-weight: 400;
  font-size: 20px;
  line-height: 120%;
  color: #000000;
}
.order-history-card__caption {
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  color: #A1AEB3;
}
.order-history-card__heading {
  @media (max-width: 420px) {
    margin-bottom: 28px;
  }
}
.order-history-card__buttons {
  @media (max-width: 768px) {
    display: flex;
    flex-direction: column-reverse;
    align-items: flex-end;
    justify-content: flex-start
  }
  @media (max-width: 420px) {
    align-items: flex-start;
  }
}
.order-history-card__button-track {
  margin-right: 8px;
  @media (max-width: 768px) {
    margin-top: 16px;
    margin-right: 0
  }
}

.order-history-card__info {
  display: grid;
  column-gap: 120px;
  row-gap: 30px;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 0px;
  transition: all 0.3s ease;
  transform-origin: top;
  transform: scaleY(0);
  opacity: 0;
  pointer-events: none;
  max-height: 1px;
  overflow: hidden;
  @media (max-width: 768px) {
    grid-template-columns: 1fr;
    row-gap: 32px;
    padding-left: 12px;
    padding-right: 12px;
  }
}

.order-history-card__info-row {
  display: grid;
  grid-template-columns: 203px auto;
  column-gap: 36px;
  margin-bottom: 16px;
  font-family: "RF Dewi", sans-serif;
  @media (max-width: 420px) {
    grid-template-columns: 113px auto;
  }
}
.order-history-card__info-row:last-of-type {
  margin-bottom: 0;
}
.order-history-card__info-row:first-child {
  margin-bottom: 41px;
  @media (max-width: 768px) {
    margin-bottom: 32px
  }
}
.order-history-card__info-row:first-child .order-history-card__info-title {
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  color: #A1AEB3;
}

.order-history-card__products {
  background: #F0F3F5;
  border-radius: 6px;
  padding: 20px;
  @media (max-width: 768px) {
    padding: 16px
  }
}
.order-history-card__products-title {
  font-weight: 400;
  font-size: 20px;
  line-height: 120%;
  color: #000000;
  margin-bottom: 0px;
  transition: all 0.3s ease;
  transform-origin: top;
  transform: scaleY(0);
  opacity: 0;
  pointer-events: none;
  max-height: 1px;
  overflow: hidden;
  @media (max-width: 768px) {
    padding-right: 12px;
    padding-left: 12px;
  }
}

.order-history-card:not(.order-history-card--opened) .order-history-card__products-gallery {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  column-gap: 8px;
  margin-bottom: 18px;
  @media (max-width: 768px) {
    grid-template-columns: repeat(4, 1fr);
    margin-bottom: 12px;
  }
}

.order-history-card__products-more {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  border-radius: 6px;
  font-weight: 400;
  font-size: 27px;
  line-height: 110%;
  color: #5F6C75;
  width: 100%;
  cursor: pointer;
  @media (max-width: 420px) {
    font-size: 20px;
  }
}

.order-history-card__total-short {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 6px;
  transition: all 0.3s ease;
  transform-origin: top;
  max-height: 1000px;
  overflow: visible;
}
.order-history-card__total-short-summary {
  @media (max-width: 420px) {
    max-width: 140px;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
  }
}

.order-hisotry-card__total-full {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 8px;
  transform-origin: top;
  transform: scaleY(0);
  opacity: 0;
  pointer-events: none;
  overflow: hidden;
  max-height: 1px;
  @media (max-width: 768px) {
    grid-template-columns: 1fr;
    row-gap: 8px;
  }
}

.order-history-card__total-card {
  margin-bottom: 0;
  @media (max-width: 768px) {
    padding-top: 32px;
    padding-bottom: 32px;
  }
}
.order-history-card__total-card .cart-total__row {
  @media (max-width: 768px) {
    margin-bottom: 8px;
  }
}
.order-history-card__total-card .cart-total__row:last-of-type {
  @media (max-width: 768px) {
    margin-bottom: 0;
  }
}
.order-history-card__total-card:last-of-type .cart-total__row:last-of-type {
  @media (max-width: 768px) {
    margin-top: 30px;
  }

}
// Modificatoins


.order-history-card:not(.order-history-card--opened) .order-history__product {
  padding: 0;
  // max-width: 157px;
  max-width: 100%;
  max-height: 162px;
  margin-bottom: 0;
  @media (max-width: 420px) {
    max-height: 72px;
  }
}
.order-history-card:not(.order-history-card--opened) .order-history__product:nth-child(n+6) {
  @media (min-width: 769px) {
    display: none;
  }
}
.order-history-card:not(.order-history-card--opened) .order-history__product:nth-child(n+4) {
  @media (max-width: 768px) {
    display: none;
  }
}
.order-history-card:not(.order-history-card--opened) .order-history__product .product-card__pic {
  margin-right: 0px;
  height: 100%;
  width: 100%;
  @media (max-width: 420px) {
    padding: 10px;
  }
}
.order-history-card:not(.order-history-card--opened) .order-history__product .product-card__info {
  max-width: 0px;
  overflow: hidden;
  transform: scaleX(0);
  transform-origin: left;
}

.order-history-card:not(.order-history-card--opened) .order-history__product .product-card__inner {
  @media (max-width: 420px) {
    padding: 0;
  }
}

.order-history__product .product-card__price {
  font-size: 20px;
  font-weight: 400;
  @media (max-width: 768px) {
    font-size: 14px
  }
}

// Opened

.order-history-card--opened .order-history-card__top {
  margin-bottom: 67px;
  @media (max-width: 768px) {
    margin-bottom: 32px;
  }
}
.order-history-card--opened .order-history-card__info {
  pointer-events: initial;
  transform: scaleY(1);
  opacity: 1;
  max-height: initial;
  overflow: initial;
  margin-bottom: 70px;
  @media (max-width: 768px) {
    margin-bottom: 32px;
  }
}
.order-history-card--opened .order-history-card__products-title {
  pointer-events: initial;
  transform: scaleY(1);
  opacity: 1;
  max-height: initial;
  overflow: initial;
  margin-bottom: 28px;
  @media (max-width: 768px) {
    margin-bottom: 32px
  }
}

.order-history-card--opened .order-history-card__products {
  background-color: transparent;
  padding-top: 0;
  @media (max-width: 768px) {
    padding-left: 0;
    padding-right: 0;
  }
}
.order-history-card--opened .order-hisotry-card__total-full {
  pointer-events: initial;
  transform: scaleY(1);
  opacity: 1;
  max-height: initial;
  overflow: initial;
}

.order-history-card--opened .order-history-card__products-more {
  display: none;
}

.order-history-card--opened .order-history-card__total-short {
  transform: scaleY(0);
  opacity: 0;
  pointer-events: none;
  max-height: 1px;
  overflow: hidden;
  margin-bottom: 0;
}
.order-history-card--opened .order-history__product:last-of-type {
  margin-bottom: 0;
}

.order-history-card__total-button-repeat:hover {
  @media (any-hover:hover) {
    --button-bg: #000;
  }
}
.order-history-card__button-cancel {
  --button-color: var(--c-text-on);
}

.products--layout-horizontal .order-history__product .product-card__inner {
  @media (max-width: 768px) {
    padding: 4px
  }
}
.order-history-card--opened .order-history__product .product-card__pic {
  @media (max-width: 768px) {
    margin-right: 12px;
  }
}
