.promo-gallery {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 8px;
  @media (max-width: 810px) {
    grid-template-columns: 1fr;
    row-gap: 8px;
  }
}

.promo-gallery-card {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  min-height: 248px;
  padding: 24px 61px 24px 20px;
  background-color: #fff;
  border-radius: 12px;
  @media (max-width: 1200px){
    padding-right: 100px
  }
  @media (max-width: 610px) {
    min-height: 188px;
  }
}
.promo-gallery-card__top,
.promo-gallery-card__bottom {
  position: relative;
  z-index: 2;
}
.promo-gallery-card__title {
  max-width: 269px;
  margin-bottom: 8px;
  font-size: 20px;
  font-weight: 400;
  line-height: 120%;
  color: #000000;
  @media (max-width: 601px) {
    font-size: 16px
  }
}

.promo-gallery-card__desc {
  max-width: 220px;
  font-family: "RF Dewi", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
  color: #a1aeb3;
}

.promo-gallery-card__pic {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
}
.promo-gallery-card__img {
  border-radius: 12px;
  object-fit: cover;
}

.promo-gallery-card--no-pic {
  @media (max-width: 1200px) {
    padding-right: 20px
  }
}
.promo-gallery-card--no-pic .promo-gallery-card__pic {
  display: none;
}
.promo-gallery-card--no-pic .promo-gallery-card__title {
  max-width: 100%;
}
.promo-gallery-card--no-pic .promo-gallery-card__desc {
  max-width: 100%;
}
.promo-gallery-card--accent {
  background-color: var(--c-accent);
}
.promo-gallery-card--accent .promo-gallery-card__title {
  color: #ffffff;
}
