.grabber-carousel {
  max-width: calc(100% - 159px);
  width: 100%;
  margin-left: auto;
  margin-right: 0;
  overflow: visible;
}
.grabber-carousel .swiper-wrapper {
  overflow-x: visible;
}
.grabber-carousel__slide {
  max-width: 470px;
}
.grabber-carousel__slide-media {
  max-width: 100%;
  height: 315px;
  margin-bottom: 20px;
  display: block;
}
.grabber-carousel__slide-pic {
  display: block;
  height: 100%;
}
.grabber-carousel__slide-img {
  display: block;
  height: 100%;
  border-radius: 12px;
  object-fit: cover;
}
.grabber-carousel__slide-title {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
  margin-bottom: 8px;
}
.grabber-carousel__slide-desc {
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: #5F6C75;
  opacity: 0.6;
}
